import { Link } from "react-router-dom";
import { useState } from "react";

const Register = ({ onRegister }) => {
	const [email, setEmail] = useState("");
	const [password, setPassword] = useState("");

	const handleChangeEmail = (evt) => {
		setEmail(evt.target.value);
	};

	const handleChangePassword = (evt) => {
		setPassword(evt.target.value);
	};

	const handleSubmit = (evt) => {
		evt.preventDefault();
		onRegister(email, password);
	};

	return (
		<form className="auth-form" name="register" onSubmit={handleSubmit}>
			<h2 className="auth-form__title">–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</h2>
			<input
				type="email"
				id="email"
				className="auth-form__input"
				name="email"
				placeholder="Email"
				value={email}
				onChange={handleChangeEmail}
				minLength="2"
				maxLength="40"
				required
			/>
			<input
				type="password"
				id="password"
				className="auth-form__input"
				name="password"
				placeholder="–ü–∞—Ä–æ–ª—å"
				value={password}
				onChange={handleChangePassword}
				minLength="2"
				maxLength="40"
				required
			/>
			<button type="submit" className="auth-form__register-button">
				–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è
			</button>
			<Link to="/signin" className="auth-form__link">
				–£–∂–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω—ã? –í–æ–π—Ç–∏
			</Link>
		</form>
	);
};

export default Register;
